.home{
    background-image: url('../assets/IMG_20190714_170240.jpg');
    filter: brightness(80%);
    background-repeat: no-repeat ; 
    background-size : cover;
    height : 100vh ; 
    width :100vw ;
}
.header{
  
    position: fixed;
    top : 0 ; 
    height : 8vh ;
    width : 100vw;
    transition: background-color 0.8s ease;
    z-index : 1000;
    color :  $White;
    
    div{
        padding:1vh 2vw ;
        border-radius: 10px;
        cursor: pointer;
        &:hover{
            background-color : $White-opa;
            color : $Black;
        }
    }
    .logo{
        position:relative;
        top : -3px;
        padding:0vh 0vw ;
        width:6vh;
        height:6vh;
        background-image: url('../assets/LOGO1.png');
        background-repeat: no-repeat ; 
        background-size : cover;
       
       
    }
    .logo-container{
        padding:0vh 0vw ;
        margin : 0vh 5vw 0vh 2vh;
        background-color: $White-opa;
        background-size : cover;
         border-radius: 10px;
         cursor: pointer;
         width:fit-content;
    }
}
.header-background{
    background-color : rgba(0, 0, 0, 0.726);
  
    
}
.body1{
    margin : 10vh 0vw;
}
.body2{
