}
.arrow-down {
    z-index: 50;
    background-image: url('../assets/arrow-down.png');
    background-repeat: no-repeat ; 
    background-size : cover;
    position :relative;
    left : 50%;
    top : 85vh;
    height : 50px;
    width : 50px;
    cursor : pointer;
    animation: 3s ease-in 1s infinite ;
    animation-name: fadeInDown;
  
    
}
.parallax1{
    background-image: url('../assets/speaker.jpg');
    background-repeat: no-repeat ; 
    background-size : cover;
    position :absolute;
    top :-50vh;
    height:150vh;
    width : 100%;
    color : $White ;
    z-index: 1;
    transition: transform 500ms linear;
 
}
.parallax2{
    background-image: url('../assets/speakerWall.jpg');
    background-repeat: no-repeat ; 
    background-size : cover;
    position :absolute;
    height:150vh;
    top :-50vh;
    color : $White ;
    width : 100%;
    z-index: 10;
    transform: translateY(0vh);
    transition: transform 500ms linear;
}
.parallax3{
    background-color: $Black-opa;
