        background-color : $Black ;
    }
    
}
.cursive{
    font-family: $font-2;
}
.black-button{
    color : $White ;
    border : 2px solid $Black ;
    padding : 1vh 3vw;
    background-color : $Black;
    width :fit-content;
    cursor: pointer;
    margin: 0.5vh 0vw;
    &:hover{ 
