    }

}
.arrow-left{
    background-image: url('../assets/right-arrow.png');
    background-repeat: no-repeat ; 
    background-size : cover;
    transform: rotate(180deg);
    height : 40px ;
    width : 40px;
    cursor: pointer;
    animation: 4s ease-in 1s infinite ;
    animation-name: fadeLeft;

}
.arrow-right{
    background-image: url('../assets/right-arrow.png');
    background-repeat: no-repeat ; 
    background-size : cover;
    height : 40px ;
    width : 40px;
    cursor: pointer;
    animation: 4s ease-in 1s infinite ;
    animation-name: fadeRight;

}
.profile1{
    background-image: url('../assets/chat.jpg');
    background-repeat: no-repeat ; 
    border-radius: 100%;
    background-size : cover;
    height : 20vh ;
    width : 20vh;
 
}

.profile2{
    background-image: url('../assets/lion.jpg');
    background-repeat: no-repeat ; 
    background-size : cover;
    border-radius: 100%;
    height : 20vh ;
    width : 20vh;
    
}
.apear{
 
    animation: 2s ease-in 0s 1 ;
    animation-name: apear;
}
.disapear{
 
    animation: 2s ease-in 0s 1 ;
    animation-name: disapear;
   
}
.content2{
    
 
}

@keyframes fadeInDown {
    0% {
       opacity: 1;
       transform: translateY(0);
    }
    100% {
       opacity: 0;
       transform: translateY(10vh);
    }
 } 
 @keyframes fadeLeft {
    0% {
       opacity: 1;
       transform: translateX(0) rotate(180deg);
      
    }
    75% {
        opacity: 0;
        transform: translateX(-10vh) rotate(180deg);
     
     }
     85%{
         opacity: 0;
         transform: translateX(0)rotate(180deg);
     }
    100% {
       opacity: 1;
       transform: translateX(0vh) rotate(180deg);
    
    }
 } 
 @keyframes fadeRight {
    0% {
       opacity: 1;
       transform: translateX(0) ;
      
    }
    75% {
       opacity: 0;
       transform: translateX(10vh) ;
    
    }
    85%{
        opacity: 0;
        transform: translateX(0)
    }
    100%{
        opacity: 1;
        transform: translateX(0)
    }
 } 

 @keyframes apear {
    0% {
       opacity: 0;
       transform: translateY(10vh)
    }
    100% {
       opacity: 1;
       transform: translateY(0vh)
    }
 } 
 @keyframes disapear {
    0% {
       opacity: 1;
       transform: translateY(0vh)
    }
    100% {
       opacity: 0;
       transform: translateY(10vh)
    }
 } 
@media only screen and (max-width: 900px){
    
 .head{
    position : absolute;
    top:-110vh;
    flex-direction : column;
    height: 100vh;
    width : 100vw;
    background-color : rgba(0, 0, 0, 0.726);
    transition : all 2s ease-in-out;
    border-bottom: 10px solid $White ;

 }
 .header{
   
     display: flex;
     justify-content:start !important;
     align-items: start !important;
     flex-direction : column;
     
   
     
 }
.logo-container{
    margin : 1vh 0vw 0vh 2vw !important;
    z-index : 1010;
}
.header-background{
    background-color: transparent;
}

}

 .hiden{
    display: none !important;
}