html{
    background: $White;
    
    background-repeat: no-repeat;
    display: flex;
    background-size :cover;
    justify-content: center;
    align-items: center;
    overflow-x : scroll ;
    
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: $font-3;
}
body{
    
     background: $White;
    background-repeat: no-repeat;
    background-size :cover;
    overflow: hidden;
    height : 100%;
    width : 100vw;
    user-select: none;

}

#root{
    height: 100%;
}
a {
    font-size: 17px;
    text-decoration: none;
    color: $Black;
    cursor: pointer;
    &:visited {
        color: $White-opa;
    }
    &hover{
        color:black;
    }
}


    input{
        display: block;
    width: 100%;
    padding: .375rem .75rem;
    font-size: xx-large;
    line-height: 1.5;
    color: #495057;
    background-color: $White-opa;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    }
select{
    display: block;
    width : 200px;
    font-size:xx-large;
    line-height: normal;
    color: #495057;
    background-color: $White-opa;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

::-webkit-scrollbar {
   display: none;
  }
   
 
